<template>
  <div class="member">
        <strong class="title">王的部落联赛积分管理 BETA版</strong>
        <div class="main">
            <el-table
                class="tableClass"
                :data="tableData"
                height="calc(100vh - 50px)"
                style="width: 100%">
                
                <el-table-column
                v-for="(item, index) in tableColumn" :key="index"
                :prop="item.key"
                :fixed="item.fixed"
                :label="item.label"
                :width="item.width">

                <template slot-scope="scope">
                    <span class="font">{{ scope.row[item.key] }}</span>  
                </template>
                </el-table-column>
            </el-table>
        </div>
  </div>
</template>

<script>
import { memberArray } from '../assets/data/order0211'
export default {
  name: 'Member',
  data() {
    return {
        tableData: [],
    }
  },
  computed: {
    tableColumn() {
        return [
            { label: '昵称', key: 'name', width: '100px', fixed: true },
            { label: 'id', key: 'id', width: '110px' },
            { label: '积分', key: 'point', width: '50px ' },
            { label: '额外奖励次数', key: 'prizeTime', width: '100px' },
            // { label: '默认顺序', key: '', width: '' },
        ]
    }
  },
  created() {
    let memberArrayFilter = memberArray.filter(item => item.name)
    let memberArrayFilter1 = memberArrayFilter.filter(item => item.point>6)
    let memberArrayFilter2 = memberArrayFilter.filter(item => item.point<7)
    memberArrayFilter.sort((a, b) => {
        if(a.prizeTime === b.prizeTime) {
            return b.iii - a.iii
        } else {
            return a.prizeTime - b.prizeTime
        }
    })
    memberArrayFilter2.sort((a, b) => {
        if(a.prizeTime === b.prizeTime) {
            return b.iii - a.iii
        } else {
            return a.prizeTime - b.prizeTime
        }
    })
    this.tableData = memberArrayFilter.concat(memberArrayFilter2)
  }
}
</script>

